import React from 'react'
import Button from 'react-bootstrap/Button';
import { Link } from 'react-router-dom';
import "./cartouche.css";

const Cartouche = ({home}) => {
  return (
    <div id="cartoucheContainer">
          <div>
            <div className="page-header header-cartouche">
              <div className="col-sm-10"><img className="imgLogo" data-key="" src= {home ? `../logo.png` : `../../logo.png`} alt="Logo"/></div> {/*window.location.protocol + `//` + window.location.hostname +`/coco_bot/front_bot/src/logo.png` */}
              <div className="headerAccueil">
                <Button variant="secondary">
                  <Link to={{pathname: "/home/"}}>
                    Home
                  </Link>
                </Button>
                <Button variant="secondary">About</Button>
              </div>
            </div>
          </div>
          <div id="cartoucheContainerWord"></div>
    </div>
  )
}

export default Cartouche