import React from 'react'
import "./footer.css";

const Footer = ({}) => {
  return (
<footer class="footerContainer">
    <div class="container  py-5">
      <div class="row py-4">
        <div class="col-lg-4 col-md-6 mb-4 mb-lg-0">
          <p class="font-italic text-muted"></p>
          <ul class="list-inline mt-4">
            <li class="list-inline-item"><a href="#" target="_blank" title="twitter"><i class="fa fa-twitter"></i></a></li>
          </ul>
        </div>
        <div class="col-lg-4 col-md-6 mb-lg-0">
          <h6 class="text-uppercase font-weight-bold mb-4">Newsletter (not functional yet)</h6>
          <p class="text-muted mb-4">If you have any comments, send them to us ! Thank you</p>
          <div class="p-1 rounded border">
            <div class="input-group">
              <input type="email" placeholder="Enter your comments" aria-describedby="button-addon1" class="form-control border-0 shadow-0"/>
              <div class="input-group-append">
                <button id="button-addon1" type="submit" class="btn btn-link"><i class="fa fa-paper-plane"></i></button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="footerContainer py-4">
      <div class="container text-center">
        <p class="text-muted mb-0 py-2">© 2022 Cocodlbot All rights reserved.</p>
      </div>
    </div>
  </footer>
  )
}

export default Footer